    <p class="information section">

    The public domain consists of all the creative work to which no exclusive intellectual property rights apply.  The works are owned by the public. Those rights may have expired, been forfeited, expressly waived, or may be inapplicable.

    As examples, the works of William Shakespeare and Ludwig van Beethoven, and most early silent films, are in the public domain either by virtue of their having been created before copyright existed, or by their copyright term having expired. 
    Some works are not covered by copyright, and are therefore in the public domain—among them the formulae of Newtonian physics, cooking recipes, and all computer software created prior to 1974. The term public domain is not normally applied to situations where the creator of a work retains residual rights, in which case use of the work is referred to as "under license" or "with permission".
